import React, { useState, useEffect } from 'react';
const axios = require('axios')




const App = () =>{


  useEffect(() => {

    get_players()
    .then(res => setPlayers(res.data))
    
    

  }, [])


  const [players, setPlayers] = useState([])
  const [playerToAdd, setPlayerToAdd] = useState('')
  const [scoreToAdd, setScoreToAdd] = useState('')
  const [sortType, setSortType] = useState('score')

  
  const get_players = async () => {
    const a = await axios.get('http://localhost:3000/Players')
    console.log(a.data)
    return a

  }





  const sortPlayers = () => {

    if (sortType === 'score'){

      (players.sort((a,b) => (a.score > b.score) ? 1 : -1))
    }

    if (sortType === 'name'){
      (players.sort((a,b) => (a.name > b.name) ? 1 : -1))
    }

    setSortType(sortType == 'score' ? 'name' : 'score') 


  }
  

  const addPlayer = () => {

    if((players.filter(p => p.name.toUpperCase() === playerToAdd.toUpperCase()).length)){

      const message = window.confirm("Player already in scoreboard, do you want to update score?")

      if(message){
        
        setPlayers(players.filter(p => p.name.toUpperCase() != playerToAdd.toUpperCase()).concat({name : playerToAdd.charAt(0).toUpperCase() + playerToAdd.slice(1),
          score : scoreToAdd })) 
          const player_id = players.filter(p => p.name.toUpperCase() === playerToAdd.toUpperCase())[0].id
          

          console.log(playerToAdd )
          console.log(player_id)
          if(playerToAdd && scoreToAdd){
            axios.put('http://localhost:3000/Players/'+player_id, {score:scoreToAdd})
          

          }
          
          else{
            window.confirm('player needs a name and a score')
          }
        
          
      }

    }
    else{
      if(playerToAdd && scoreToAdd){
        setPlayers(players.concat({name : playerToAdd.charAt(0).toUpperCase() + playerToAdd.slice(1),
          score : scoreToAdd }))
  
        axios.post('http://localhost:3000/Players', {name : playerToAdd.charAt(0).toUpperCase() + playerToAdd.slice(1),
        score : scoreToAdd })
        
      }
      else{
        window.confirm('player needs a name and a score')
      }
      
    }


    setPlayerToAdd('')
    setScoreToAdd('')

  }

  if(players.length){

  

  return(

    <div>
      Players:
      <button onClick={() => sortPlayers()}> Sort by {sortType} </button>

      {players.map(p => 
        <p>
          Player {p.name}  {p.score} points
        </p>
        )}

      <div>
        Name
        
        <input value={playerToAdd} onChange={(event) => setPlayerToAdd(event.target.value)} />

        <div>
          Score
          <input value={scoreToAdd} onChange={(event) => setScoreToAdd(event.target.value)} />

        </div>
        
        <button type='submit' onClick={() => addPlayer()} >Click to add player</button>

      </div>

    </div>




  )}
  else{
    return(

      <div>
        Name
        
        <input value={playerToAdd} onChange={(event) => setPlayerToAdd(event.target.value)} />

        <div>
          Score
          <input value={scoreToAdd} onChange={(event) => setScoreToAdd(event.target.value)} />

        </div>
        
        <button type='submit' onClick={() => addPlayer()} >Click to add player</button>

      </div>

    )
  }
  

  

}

export default App
